  function addText(toRemix) {
    console.log(stage.getLayers());
    console.log(layer.getChildren());
    if (toRemix === true) {
      //console.log("REMIX ON:- "+typeof(txtColor));
      //https://konvajs.org/api/Konva.Shape.html#fill
      for (let i = 0; i < textElementArray.length; i++) {
        let color = generateRandomTextColor();
        let fontStyle = generateRandomFontStyle();
        textElementArray[i].fill(color);
        textElementArray[i].fontFamily(fontStyle);
      }
    } else {
      userText = new Konva.Text({
        x: 15,
        y: 15,
        ellipsis: true,
        text: document.getElementById("userText").value,
        fontSize: document.getElementById("userTextSize").value,
        fill: document.getElementById("userTextColor").value,
        draggable: true,
        name: "object",
      });
    }

    userText.on("dblclick", () => {
      // create textarea over canvas with absolute position

      // first we need to find position for textarea
      // how to find it?

      // at first lets find position of text node relative to the stage:
      var textPosition = userText.getAbsolutePosition();

      // then lets find position of stage container on the page:
      var stageBox = stage.container().getBoundingClientRect();

      // so position of textarea will be the sum of positions above:
      var areaPosition = {
        x: stageBox.left + textPosition.x,
        y: stageBox.top + textPosition.y,
      };

      // create textarea and style it
      var textarea = document.createElement("textarea");
      document.body.appendChild(textarea);

      textarea.value = userText.text();
      textarea.style.position = "absolute";
      textarea.style.top = areaPosition.y + "px";
      textarea.style.left = areaPosition.x + "px";
      textarea.style.width = userText.width();

      textarea.focus();

      textarea.addEventListener("keydown", function (e) {
        // hide on enter
        if (e.keyCode === 13) {
          userText.text(textarea.value);
          layer.draw();
          document.body.removeChild(textarea);
        }
      });
    });

    layer.add(userText);
    stage.add(layer);
    layer.moveToTop();

    textElementArray.push(userText);
    layer.draw();
  }

  function makeTextBold() {
    let fontStyle = userText.fontStyle();

