  function addBlobs() {
    // I generate the d value in <path> from Blob.svelte's generateColor

    let colorArr = colorPalette();
    for (let i = 0; i < 5; i++) {
      let SVG =
        '<svg xmlns="http://www.w3.org/2000/svg"> <path fill="' +
        colorArr[i + 1] +
        '" d="' +
        svgPaths[i] +
        '" transform="translate(100 100)" /> </svg>';
      let url = "data:image/svg+xml;base64," + window.btoa(SVG);
      Konva.Image.fromURL(url, (image) => {
        image.draggable(true);
        image.x(returnRandom());
        image.y(returnRandomY());
        stage.add(Shapelayer);
        Shapelayer.add(image);

        // If you uncomment below option, it causes the blobs to be not added in the stage. it shows in editor but not downloaded image.
        //Shapelayer.moveToBottom();
      });
    }
  }

