<script>
  import {
    Form,
    FormGroup,
    FormText,
    Input,
    Label,
    Row,
    Col,
    Container,
    Jumbotron,
    Button,
    Navbar,
    NavbarBrand,
    Nav,
    NavItem,
  } from "sveltestrap";
  import { Router, Link, link, Route } from "svelte-routing";

  import Footer from "./Footer.svelte";
  import { testCode } from "./SumoCodes.svelte";
  import { onMount } from "svelte";
  import { beforeUpdate, afterUpdate } from "svelte";
  import { auth, db, firebaseAnalytics } from "./firebase";

  function isEmpty(obj) {
    for (var key in obj) {
      if (obj.hasOwnProperty(key)) return false;
    }
    return true;
  }

  function addAccountToFirebase() {
    // first we will create account then add it in PROdb of firebase.
    //get data from form
    let name = document.getElementById("username").value;
    let email = document.getElementById("email").value;
    let pass = document.getElementById("password").value;
    let code = document.getElementById("appsumocode").value;

    auth
      .createUserWithEmailAndPassword(email, pass)
      .catch(function (error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // ...
      })
      .then(() => {
        console.log("Account created!");
        auth.signInWithEmailAndPassword(email, pass).catch(function (error) {
          // Handle Errors here.
          var errorCode = error.code;
          var errorMessage = error.message;
          // ...
        });
      })
      .then(() => {
        let user = auth.currentUser;
        //now we will give the user, PRO access
        user.updateProfile({
          displayName: name,
        });
        console.log("username applied.");
        db.collection("PROusers").doc(user.uid).set({
          email: email,
          checkoutId: code,
          planType: "AppSumo LifeTime",
          SubscriptionDate: Date(),
        });
        console.log("Account added in db.");
      })
      .then(() => {
        alert(
          "Your Account was created successfully! Login & Enjoy your Lifetime Access :)"
        );
        //Redirect();
      });
  }

  function Redirect() {
    window.location = "http://www.designtack.com/";
  }

  function submitFunction(e) {
    e.preventDefault();
    //console.log("got code --"+document.getElementById('appsumocode').value);
    //testCode();

    let name = document.getElementById("username").value;
    let email = document.getElementById("email").value;
    let pass = document.getElementById("password").value;
    let code = document.getElementById("appsumocode").value;

    if (name == "" || email == "" || pass == "" || code == "") {
      alert("One or more fields are empty");
    } else {
      let c = document.getElementById("appsumocode").value;
      let url =
        "https://api.sheety.co/46f1b341d8cbdb82a6dc567068c9894f/redemptionCodesForDesigntack/sheet?filter[code]=" +
        c;
      try {
        fetch(url)
          .then((response) => response.json())
          .then((json) => {
            // Do something with the data
            let data = json.sheet;
            //console.log(data+typeof(data));
            if (data.length > 0) {
              //this means code found in our spreadsheet file,
              //TODO - write here the code which will initiate the registration in firebase process + edit the row and add a prefix "CODEUSED" to code field
              data = json.sheet[0];
              console.log(data.code + " " + data.id);
              //below code to overwrite the code
              let url =
                "https://api.sheety.co/46f1b341d8cbdb82a6dc567068c9894f/redemptionCodesForDesigntack/sheet/" +
                data.id;
              let body = {
                sheet: {
                  code: "CODEUSED" + data.code,
                  id: data.id,
                },
              };
              fetch(url, {
                method: "PUT",
                body: JSON.stringify(body),
                headers: { "Content-Type": "application/json" },
              })
                .then((response) => response.json())
                .then((json) => {
                  // Do something with the data
                  //console.log(json);
                });

              //calling function to create account in PROdb and in firebase
              addAccountToFirebase();
            } else {
              //this means code NOT found in our spreadsheet file
              alert(
                "Oops! The code you entered is used or invalid. Please try again or Contact us."
              );
            }
          });
      } catch (err) {}
    }
  }
</script>

<svelte:head>
  <link
    rel="stylesheet"
    href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css"
  />
  <link
    rel="stylesheet"
    href="https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css"
  />
</svelte:head>
<div>
  <Container fluid>
    <Row class="pt-5">
      <Col sm="12" class="p-3">
        <p class="display-4 textBlue font-weight-bold text-center">
          DT ♥ AppSumo
        </p>
      </Col>
      <Col sm="12" class="text-center">
        <h4>Fill the form below with your information.</h4>
      </Col>
    </Row>
    <Form method="POST">
      <Row class="pt-5">
        <Col sm="12" class="text-center">
          <FormGroup>
            <Label class="font-weight-bold" for="username">Your Name</Label>
            <Input type="text" name="username" id="username" required />
          </FormGroup>
        </Col>
        <Col sm="12" class="text-center">
          <FormGroup>
            <Label class="font-weight-bold" for="email">Your Email</Label>
            <Input type="email" name="email" id="email" required />
          </FormGroup>
        </Col>
        <Col sm="12" class="text-center">
          <FormGroup>
            <Label class="font-weight-bold" for="password">Your Password</Label>
            <Input type="password" name="password" id="password" required />
          </FormGroup>
        </Col>
        <Col sm="12" class="text-center">
          <FormGroup>
            <Label class="font-weight-bold" for="appsumocode"
              >Your AppSumo Code</Label
            >
            <Input type="text" name="appsumocode" id="appsumocode" required />
          </FormGroup>
        </Col>
        <Col sm="12" class="text-center">
          <button
            type="submit"
            class="btn btn-lg pressDown"
            on:click={submitFunction}>Submit & Sign in</button
          >
        </Col>
      </Row>
    </Form>
    <div class="bluishBG">
      <Row class="py-5">
        <Col sm="12" id="scrollToInfo" class="p-4">
          <p id="textWhite" class="display-4 font-weight-bold text-center">
            Start Making Content
          </p>
        </Col>
        <Col sm="12" class="text-center">
          <a href="/Login" class="pressDown btn btn-lg" use:link
            >Get Started for Free</a
          >
        </Col>
      </Row>
    </div>
    <Footer />
  </Container>
</div>

<style>
  .textBlue {
    color: #5778f3;
  }
  .bluishBG {
    background-color: #8fa7ff;
    margin-top: 5%;
  }
  #textWhite {
    color: white;
  }
  .pressDown {
    background-color: #5778f3;
    padding: 10px 30px;
    color: #fff;
    border-radius: 5px;
    border-bottom: 4px solid #3e4caf;
    transition: 0.1s;
  }

  .pressDown:hover {
    border-bottom-width: 0;
    margin-top: 3px;
    color: #fff;
  }
</style>
