                    isPro = true;
                    remaining_days = parseInt(1)
                    return;
                } else {
                    // doc.data() will be undefined in this case
                //  console.log("No such document!");
                    isPro = false;
                }
                console.log(isPro)
                }).then(()=>{
                    if(isPro === false) {
                        console.log("not pro");
                        //Now check if they are on trial or is it expired
                        let userData = doc.data();
                        console.log('returned userData',userData)
                        var tdy = new Date();
                        var trial = userData.trial_expiry_date ? userData.trial_expiry_date.toDate() : 0;
                        console.log(tdy)
                        console.log(trial)
                        // let is_expired = tdy.getTime() === trial.getTime();
                        // console.log(is_expired)
                        let days_left = parseInt((trial - tdy) / (1000 * 3600 * 24));
                        console.log(days_left)
                        remaining_days = parseInt(days_left)

                       // if expired trial, set a localstorage called "pending days left" to 0
                        if(days_left <= 0) {
                            localStorage.setItem("pdluid",0);
                            return;
                        }
                        else {
                            // the user is on free trial with {days_left} days left.
                            localStorage.setItem("pdluid",1)
                            return;
                            //this is responsible to show the end user how much time he has left for free trial.
                        }
                }
                }).catch(function(error) {
                    console.log("Error getting document:", error);
                })
        } else {
            console.log("Not exists in usersdb, creating a new user")
            //create a new user with uid, email and necessary details.
            //assign them 14 days free trial & start from today.
            let tdy = new Date();
            let expiry_date = new Date();
            expiry_date.setDate(expiry_date.getDate() + 14);

            //days left by divide the time difference of both the dates 
            //by no. of milliseconds in a day (1000*60*60*24)
            let days_left = (expiry_date - new Date()) / (1000 * 3600 * 24);

            let is_expired = tdy.getTime() === expiry_date.getTime();

            db.collection('usersDB').doc(user.uid).set({
                        totalCreated: 0,
                        email: user.email,
                        registeredDate: tdy,
                        trial_expiry_date: expiry_date,

            })
            contentMadeSoFar = 0
            remaining_days = days_left
            console.log(contentMadeSoFar,"initialising contentmadesofar")
            timesaved();
            
        }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
        
        
    })

